<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">{{ FormLabel }}</h5>
                            <button type="button" class="close" @click="CloseSubsidy">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validateSubsidy" :buttons="buttons" :schema="FormSchema"
                                :mandatorytext="$t('MandatoryString')" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';

export default {
    props: {
        EditData: {
            type: Array,
            required: true,
        },
    },
    data() {
        return {
            isLoading: false,
            FormLabel: 'Add Subsidy',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Subsidy Name',
                            name: "SubsidyName",
                            value: "",
                            placeholder: 'Enter Subsidy Name',
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: 'Subsidy Type',
                            name: "SubsidyType",
                            value: "",  // Use v-model to bind
                            placeholder: 'SubsidyType',
                            config: {
                                options: [{ value: "1", name: 'Flat' }, { value: "2", name: 'Percentage' }, { value: "3", name: 'Free Units' }],
                                onChange: this.changeSubsidyType
                            },
                            validationRules: {
                                required: true
                            },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('CustomerType'),
                            name: "CustomerType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: 'Value',
                            name: "Value",
                            value: "",
                            placeholder: 'Enter Value',
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveFrom'),
                            name: "Effectivefrom",
                            value: new Date().toLocaleDateString(),
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveTo'),
                            name: "Effectiveto",
                            value: new Date('12/31/2099').toLocaleDateString(),
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": false },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('DESCRIPTION'),
                            validationRules: { required: true, max: 6000 },
                        }
                    ]
                },                
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: "Is Update Plan Mapped Subsidy",
                            name: "UpdatePlanMapped",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [{value: "1" }],
                            }
                           ,
                        },
                    ],
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveSubsidyData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseSubsidy,
                },
            ],
        };
    },

    created: async function () {
          debugger
        var vm = this;
        
        await vm.GetDDLCustomerTypeData();
        setTimeout(function () {
            if (Array.isArray(vm.EditData) && vm.EditData.length > 0) {

                vm.FormSchema[4].Data[0].visibility = true;
                vm.FormLabel = 'Edit Subsidy';
                vm.BindSubsidyDetail();
            }
        }, 500);



    },

    methods: {
        BindSubsidyDetail: function () {

            this.$refs.validateSubsidy.updateProperty("SubsidyName", "value", this.EditData[0].Subsidy_Name);
            this.$refs.validateSubsidy.updateProperty("SubsidyType", "value", this.EditData[0].Subsidy_Type);
            this.$refs.validateSubsidy.updateProperty("CustomerType", "value", this.EditData[0].Category);
            this.$refs.validateSubsidy.updateProperty("Value", "value", this.EditData[0].Subsidy_Value);
            this.$refs.validateSubsidy.updateProperty("Effectivefrom", "value", this.EditData[0].Effective_From);
            this.$refs.validateSubsidy.updateProperty("Effectiveto", "value", this.EditData[0].Effective_To);
            this.$refs.validateSubsidy.updateProperty("Description", "value", this.EditData[0].Subsidy_Description);


        },
        CloseSubsidy() {
            this.$emit('Close');
        },

        GetDDLCustomerTypeData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=CUSTOMER_TYPE&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateSubsidy.updateProperty("CustomerType", "config.options", response.data.DATA);
                        vm.$refs.validateSubsidy.UpdateKeyValue();
                    }
                }
            });
        },

        // Method to handle changes in SubsidyType and update validation rules
        changeSubsidyType() {

            const subsidyTypeValue = this.$refs.validateSubsidy.getPropertyValue("SubsidyType", "value"); // Get the selected SubsidyType value

            this.$refs.validateSubsidy.updateProperty("Value", "value", "");
            if (subsidyTypeValue === '2') {

                this.$refs.validateSubsidy.updateProperty("Value", "validationRules", { required: true, "min_value": 0.1, "max_value": 100 });
            } else if (subsidyTypeValue === '3') {

                this.$refs.validateSubsidy.updateProperty("Value", "validationRules", { required: true, integer: true });
            }
            else {
                this.$refs.validateSubsidy.updateProperty("Value", "validationRules", { required: true, "min_value": 0, "max_value": 9999999 }); // Matches integers and decimals

            }
        },
        async SaveSubsidyData() {

            var vm = this;
            const result = await vm.$refs.validateSubsidy.validateForm.validate();
            if (result) {
                var SubsidyName = vm.$refs.validateSubsidy.getPropertyValue("SubsidyName", "value");
                var SubsidyType = vm.$refs.validateSubsidy.getPropertyValue("SubsidyType", "value");
                var CustomerType = vm.$refs.validateSubsidy.getPropertyValue("CustomerType", "value");
                var Value = vm.$refs.validateSubsidy.getPropertyValue("Value", "value");
                var EffectiveFrom = vm.$refs.validateSubsidy.getPropertyValue("Effectivefrom", "value");
                var EffectiveTo = vm.$refs.validateSubsidy.getPropertyValue("Effectiveto", "value");
                var Description = vm.$refs.validateSubsidy.getPropertyValue("Description", "value");
                var UpdateMapped = vm.$refs.validateSubsidy.getPropertyValue("UpdatePlanMapped", "value");


                var objData =
                {
                    Id: Array.isArray(vm.EditData) && vm.EditData.length > 0 ? vm.EditData[0].Id : 0,
                    SubsidyName: SubsidyName,
                    SubsidyType: SubsidyType,
                    CustomerType: CustomerType,
                    Value: Value,
                    EffectiveFrom: EffectiveFrom,
                    EffectiveTo: EffectiveTo,
                    Description: Description,
                    UpdateMapped: UpdateMapped

                }

                var obj = JSON.stringify(objData);

                await DataService.AddSubsidyData(obj).then(response => {
                    if (response.data != null) {
                        if (response.data[0] == "Success") {
                            vm.ShowAlert(vm.$t("DataSavedSuccessfully"), "success", vm.$t('Alert'));
                            vm.CloseSubsidy();
                            vm.$parent.GetSubsidyData();

                            vm.isLoading = false;
                        }
                        else {

                            vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
                            vm.isLoading = false;
                        }
                    }
                });


            }
        },


    }
};
</script>
