<template>
    <section>

        <tg-list :IsShowAction="true" ref="tgList" :showCheckBox="true" ModuleName="Billing" SubModuleCode="Subsidy"
            IdentityColumn="Id" HeaderText="Subsidy" :ListData="SubData" :listType="ListView" :HeaderData="Headers"
            :NorecordfoundText="$t('NoRecordfound')" :TotalRecords="TotalRecords" :SearchFields="leftsearchSchema"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="GetSubsidyData" :LegendArray="LegendArray"
            :SortExp="sortExp" :SortBy="sortBy" :HeaderButtons="listheaderbuttons" :CurrentPageNumber="pageNum"
            :widgets="[]" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <!-- <template v-if="data.column.COLUMN_NAME == 'status_name'">

                    <a class="px-2 bg-success text-center text-white d-block" v-if="data.row.status_name === 'Active'">
                        <span :class="data.row.status_name">
                            {{ data.row.status_name }}
                        </span>
                    </a>

                    <a class="px-2 bg-primary text-center text-white d-block"
                        v-else-if="data.row.status_name === 'Approved'">
                        <span :class="data.row.status_name">
                            {{ data.row.status_name }}
                        </span>
                    </a>

                    <a class="px-2 bg-danger text-center text-white d-block" v-else>
                        <span :class="data.row.status_name">
                            {{ data.row.status_name }}
                        </span>
                    </a>

                </template> -->
                <template v-if="data.column.COLUMN_NAME == 'Status_Id'">
                    <div>
                        <listing-status :isfor="'SUBSIDY'" ref="updatestatus" v-bind:dataid="data.row.Id"
                            data-controller="Billing_Rule" v-bind:statuslist="StatusListForListing"
                            v-bind:item="data.row.Status_Id" data-action="index"
                            v-bind:dataselected="data.row.Status_Id == 1001 ? 'Active' : 'Inactive'"
                            v-bind:callbackfunction="GetSubsidyData" v-bind:isselectedvaluestring="true">
                        </listing-status>
                    </div>
                    <div>
                        <span v-bind:class="getStatusClassList(data.row.Status_Id, 'Status_Id')"
                            v-if="data.row.Status_Id == 'Active'">
                            {{ data.row.Status_Id }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.Status_Id, 'Status_Id')"
                            v-if="data.row.Status_Id == 'Inactive'">
                            {{ data.row.Status_Id }}
                        </span>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Details'" >
                    <div class="text-center">
                        <a id="ancProjectDetail" :class="{ 'text-dark text-center': true }" href="javascript:;"
                            v-on:click="ShowSubsidyDetails(data.row)">
                            <span class="noti-circle noti-orange">{{ data.row.PlanCount }}</span>
                        </a>
                    </div>
                </template>

            </template>

            <template v-slot:customcard="{ data }">
                <div class="card-box cb_newdes p-3">

                    <div class="custom-control custom-checkbox custom-control-inline">
                        <input type="checkbox" :value="data.row.Id" class="chkItems custom-control-input"
                            :id="'chk_' + data.row.Id" v-model="selectedSubsidies" @change="onCheckboxChange" />
                        <label class="custom-control-label" :for="'chk_' + data.row.Id"></label>
                    </div>

                    <div class="mb-2 mt-2 d-flex justify-content-between">
                        <div class="d-flex justify-content-start">
                            <span class="mr-3 iconbglt"><em class="fa fa-gift text-success font-18"></em></span>
                            <span class="">
                                <span class="w-100 font-16 font-weight-medium float-left">{{ data.row.Subsidy_Name
                                }}</span>
                                <span class="w-100 font-13 float-left">{{ data.row.Code }}</span>
                            </span>
                        </div>
                        <div>
                            <a href="#" @click="EditSubsidy(data.row)" class="mr-3"><em
                                    class="fa fa-pencil text-success font-18"></em></a>
                            <a href="#" @click="ShowSubsidyDetails(data.row)"><em
                                    class="fa fa-eye text-info font-18"></em></a>
                        </div>
                    </div>

                    <div class="card-detail-main filterscrollForCardView mt-2">

                        <div class="card-detail d-flex justify-content-between mb-2">
                            <span>Type & Value</span>
                            <span class="text-right">
                                <span class="w-100 font-weight-medium float-left">{{ data.row.Subsidy_Value }}
                                    <span class="float-right ml-1" v-if="data.row.Subsidy_Type_Value === 1">Flat</span>
                                    <span class="float-right ml-1"
                                        v-else-if="data.row.Subsidy_Type_Value === 2">%</span>
                                    <span class="float-right ml-1" v-else>Units</span>
                                </span>
                                <span class="w-100 float-left">{{ data.row.Subsidy_Type }}</span>
                            </span>
                        </div>

                        <div class="card-detail d-flex justify-content-between mb-2">
                            <span>Category</span>
                            <span class="text-right">
                                <span class="w-100 fontweight400 float-left">{{ data.row.Category }}</span>
                                <!-- <span class="w-100 float-left">EWS</span> -->
                            </span>
                        </div>

                        <div class="card-detail d-flex justify-content-between mb-2">
                            <span>Validity</span>
                            <span class="text-right">
                                <span class="w-100 fontweight400 float-left">{{ data.row.Effective_From }} to {{
                                    data.row.Effective_To }}</span>
                            </span>
                        </div>

                        <hr>
                        </hr>

                        <div class="card-detail d-flex justify-content-between mb-2">
                            <span class="mr-3 iconbglt_text font-13">{{ data.row.status_name }}</span>
                            <!-- <span class="text-right">
                                <span class="w-100 fontweight400 float-left">09-24-2025 to 09-30-2025</span>
                            </span> -->
                        </div>
                    </div>

                </div>
            </template>

        </tg-list>


        <AddSubsidy v-if="IsAddSubsidy" @Close="Close" :EditData="EditData"></AddSubsidy>

        <SubsidyDetails v-if="IsShowSubsidyDetails" @CloseDetails="CloseDetails"
            :SubsidyDeatilsObject="SubsidyDeatilsObject">
        </SubsidyDetails>

        <SubsidyHistory v-if="IsShowSubsidyHistory" @CloseDetails="CloseHistoryDetails" :SubsidyId="SubsidyId">
        </SubsidyHistory>

    </section>
</template>

<script>
import AddSubsidy from '../../../components/Modals/AddSubsidy.vue';
import DataService from '../../../services/DataService'
import SubsidyData from './SubsidyData';
import SubsidyDetails from '../../../components/Modals/SubsidyDetails.vue';
import SubsidyHistory from '../../../components/Modals/SubsidyHistory.vue';
export default {
    components: {
        AddSubsidy, SubsidyDetails, SubsidyHistory
    },
    data() {
        return {
            ListView: ['List', 'Card'],
            isLoading: false,
            IsAddSubsidy: false,
            searchQuery: '',
            SubData: [],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            TotalPages: 0,
            sortExp: '',
            Headers: [
                {
                    COLUMN_NAME: "Subsidy_Name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Name",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "Subsidy_Type",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Type",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "Category",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Category",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Subsidy_Value",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Value",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Effective_From",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Effective From",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Effective_To",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Effective To",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Status_Id",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true
                    }

                },
                {
                    COLUMN_NAME: 'Details',
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('planmapped'),
                    DISPLAY_ORDER: 8,
                    settings: {
                        isInSlot: true
                    }
                }
            ],
            listheaderbuttons: [

                {
                    title: 'Add Subsidy',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddSubsidy',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                },
                {
                    title: 'Delete',
                    iconClass: 'fa fa-trash',
                    callbackfunction: 'DeleteSubsidy',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    isEnabledOnSelectedRow: true
                },

            ],
            listActions:
                [
                    {
                        title: 'Edit',
                        //href: '/AssetRequisition/AddPurchaseQuotation/{RequisitionId}/{Order_id}',
                        icon: 'fa fa-pencil text-info action-icon',
                        callbackfunction: 'EditSubsidy',
                        isDisabled: false,
                        additionalSpan: ''

                    },
                    {
                        title: 'ViewHistory',
                        icon: 'fa fa-history text-info action-icon',
                        callbackfunction: 'ViewHistory',
                        isDisabled: false,
                        additionalSpan: ''

                    }
                ],
            leftsearchSchema: SubsidyData.leftsearchSchema,
            EditData: [],
            IsShowSubsidyDetails: false,
            SubsidyDeatilsObject: {},
            StatusListForListing: [
                {
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
            ],
            IsShowSubsidyHistory: false,
            SubsidyId: null,
            selectedSubsidies: [],

        }
    },

    created() {

        var vm = this;
        vm.GetSubsidyData();
        SubsidyData.bindleftCommonSearchdropdown(vm, 'Billing', 'BILLING_SUBSIDY', 'CUSTOMER_TYPE');
        SubsidyData.bindleftCommonSearchdropdown(vm, 'Billing', 'BILLING_SUBSIDY', 'Subsidy_Type');
        var GetLocalView = localStorage.getItem('viewType');
        if (GetLocalView == "List" || GetLocalView == null) {
            vm.ListView = ['List', 'Card']
        } else {
            vm.ListView = ['Card', 'List']
        }
    },
    methods: {
        onCheckboxChange() {
            debugger
            // Find the Delete button from listheaderbuttons
            const deleteButton = this.listheaderbuttons.find(
                (btn) => btn.callbackfunction === 'DeleteSubsidy'
            );

            // Enable if theres at least one selection
            deleteButton.isdisabled = this.selectedSubsidies.length === 0;
        },
        getStatusClass: function (status) {
            var returnClass = "";
            switch (status) {
                case "Active":
                    returnClass = "px-2 text-center bg-success text-white d-block";
                    break;
                case "Inactive":
                    returnClass = "px-2 text-center bg-danger text-white d-block";
                    break;
                default:
                    returnClass = "";
            }
            return returnClass;
        },
        AddSubsidy() {

            var vm = this;
            vm.EditData = [];
            vm.IsAddSubsidy = true;
        },
        ShowSubsidyDetails(Object) {

            var vm = this;
            vm.SubsidyDeatilsObject = Object;
            vm.IsShowSubsidyDetails = true;
        },
        Close() {

            var vm = this;
            vm.IsAddSubsidy = false;
        },
        CloseDetails() {

            var vm = this;
            vm.IsShowSubsidyDetails = false;
        },
        CloseHistoryDetails() {

            var vm = this;
            vm.IsShowSubsidyHistory = false;
        },
        renderActions(row) {

            var self = this;

            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        if (row.status_name == 'Active') {
                            action.isVisible = true;
                        } else {
                            action.isVisible = true;
                        }
                        break;

                    case 'ViewHistory':
                        if (row.status_name == 'Active') {
                            action.isVisible = true;
                        } else {
                            action.isVisible = true;
                        }

                        break;


                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: async function (value) {

            this.pageNum = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            this.GetSubsidyData();
        },
        GetSubsidyData: function () {
            debugger
            var vm = this;
            var Url = `pageSize=${vm.pageSize}&pageNum=${vm.pageNum}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&search=${vm.searchCondition}`;
            DataService.GetSubsidyData(Url).then((response) => {
                if (response.data) {
                    const parsed = JSON.parse(response.data[0]); // parse string to object
                    vm.SubData = parsed.Data; // assign the array inside Data property

                    vm.SubData.forEach(ritem => {

                        if (ritem.PlanCount > 0) {
                            ritem.isCheckBoxDisabled = true;
                        }
                        else { ritem.isCheckBoxDisabled = false }

                    })

                }

                vm.TotalRecords = vm.SubData[0].total_records;
                vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                vm.CurrentPage = vm.pageNum;
            });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetSubsidyData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {
            this.pageSize = value.pageSize;
            this.pageNum = value.pageNum;
            this.GetSubsidyData();
        },
        commonHeaderButtonClick: function (event) {

            switch (event.callbackfunction) {
                case "AddSubsidy":
                    this.AddSubsidy();
                    break;
                case "DeleteSubsidy":
                    this.DeleteSubsidy();
                    break;

            }
        },
        actionButtonClick(callbackfunction, item, index, event) {

            switch (callbackfunction) {
                case 'EditSubsidy':
                    this.EditSubsidy(item);
                    break;
                case 'ViewHistory':
                    this.ViewHistory(item);
                    break;

            }
        },
        getStatuslistForListing: function (StatusType, lang) {

            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";

            DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    //vm.StatusListForListing = json.data;
                }
            });
        },
        EditSubsidy(Obj) {

            var vm = this;
            var Url = `Id=${Obj.Id}`;
            DataService.EditSubsidy(Url).then((response) => {
                if (response.data) {
                    const parsed = JSON.parse(response.data[0]);
                    vm.EditData = parsed.Data;

                }
                vm.IsAddSubsidy = true;


            });
        },
        DeleteSubsidy() {

            var vm = this;
            var val = "";
            vm.selectedSubsidies = [];

            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Plan")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_BILLING_SUBSIDY"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.GetSubsidyData();
                            $('.chkItems').prop('checked', false);
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Plan')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Plan')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
            }
        },
        ViewHistory(Obj) {
            debugger
            var vm = this;
            vm.SubsidyId = Obj.Id;
            vm.IsShowSubsidyHistory = true;
        },
    }
}
</script>

<style>
.cb_newdes {
    border-radius: 10px;
}

.iconbglt {
    border-radius: 10px;
    background-color: #dcfce7;
    padding: 3px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 36px;
    color: #1b6938;
}

.iconbglt_text {
    border-radius: 10px;
    background-color: #dcfce7;
    padding: 4px 8px;
    text-align: center;
    font-size: 13px;
    color: #1b6938;
    font-weight: 400;
}

.cb_newdes .card-detail {
    font-weight: 300;
}

.cb_newdes .card-detail span {
    font-size: 14px;
    width: auto;
}

.fontweight400 {
    font-weight: 400;
    color: #303030;
}
</style>
