<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">Subsidy History</h5>
                            <button type="button" class="close" @click="CloseSubsidyDetails">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">

                            <tg-list :IsShowAction="false" ref="tgList" :showCheckBox="false" ModuleName="Billing"
                                SubModuleCode="SubsidyPlan" IdentityColumn="Id" HeaderText="Subsidy History"
                                :ListData="SubsidyHistoryData" :listType="['List']" :HeaderData="Headers"
                                :NorecordfoundText="$t('NoRecordfound')" :TotalRecords="TotalRecords"
                                :SearchFields="leftsearchSchema" :RenderRowActionMethod="renderActions"
                                :ListDataCallBackFunction="GetSubsidyHistoryData" :LegendArray="LegendArray"
                                :SortExp="SortExp" :SortBy="SortBy" :HeaderButtons="listheaderbuttons"
                                :CurrentPageNumber="pageNum" :widgets="[]" v-on:LeftsearchButtonClick="false"
                                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">

                                <template v-slot:slotdata="{ data }">
                                    <template v-if="data.column.COLUMN_NAME == 'status_name'">

                                        <a class="px-2 bg-success text-center text-white d-block"
                                            v-if="data.row.status_name === 'Active'">
                                            <span :class="data.row.status_name">
                                                {{ data.row.status_name }}
                                            </span>
                                        </a>

                                        <a class="px-2 bg-primary text-center text-white d-block"
                                            v-else-if="data.row.status_name === 'Inactive'">
                                            <span :class="data.row.status_name">
                                                {{ data.row.status_name }}
                                            </span>
                                        </a>

                                    </template>

                                    <template v-if="data.column.COLUMN_NAME == 'Created_At'">
                                        <span>
                                            {{ $options.filters.formatDateTime(data.row.Created_At) }}
                                        </span>
                                    </template>


                                </template>

                            </tg-list>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';

export default {
    props: {
        SubsidyId: {
            type: Number,
            require: true
        }

    },

    data() {
        return {
            isLoading: false,
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            TotalPages: 0,
            sortExp: '',
            SubsidyHistoryData: [],
            Headers: [
                {
                    COLUMN_NAME: "Subsidy_Name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Name",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "Subsidy_Type",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Type",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "Category",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Category",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Subsidy_Value",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subsidy Value",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Effective_From",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Effective From",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Effective_To",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Effective To",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Created_At",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created At",
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    COLUMN_NAME: "status_name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true
                    }

                }
            ],

        };
    },

    created: async function () {
        debugger
        var vm = this;
        vm.SubsidyId;
        vm.GetSubsidyHistoryData();

    },

    methods: {

        CloseSubsidyDetails() {
            this.$emit('CloseDetails');
        },
        pagerMethod: function (value) {
            this.pageSize = value.pageSize;
            this.pageNum = value.pageNum;
            this.GetSubsidyPlanData();
        },
        GetSubsidyHistoryData: function () {
            debugger
            var vm = this;
            var Url = `Subsidy_Id=${vm.SubsidyId}&pageSize=${vm.pageSize}&pageNum=${vm.pageNum}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&search=${vm.searchCondition}`;
            DataService.GetSubsidyHistoryData(Url).then((response) => {
                if (response.data) {
                    const parsed = JSON.parse(response.data[0]); // parse string to object
                    vm.SubsidyHistoryData = parsed.Data; // assign the array inside Data property              

                }
                vm.TotalRecords = vm.SubsidyHistoryData[0].total_records;
                vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                vm.CurrentPage = vm.pageNum;
            });
        },


    }
};
</script>
