<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">Subsidy Details</h5>
                            <button type="button" class="close" @click="CloseSubsidyDetails">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <div class="row">

                                <div class="col-md-4 col-lg-3">
                                    <div class="form-group">
                                        <label><b>Subsidy Name : </b> <span>{{ SubsidyDeatilsObject.Subsidy_Name
                                                }}</span>
                                        </label>
                                    </div>
                                </div>
                                <div class="col-md-4 col-lg-3">
                                    <div class="form-group">
                                        <label><b>Subsidy Type : </b> <span>{{ SubsidyDeatilsObject.Subsidy_Type
                                                }}</span> </label>
                                    </div>
                                </div>
                                <div class="col-md-4 col-lg-3">
                                    <div class="form-group">
                                        <label><b>Subsidy Value : </b> <span>{{ SubsidyDeatilsObject.Subsidy_Value
                                                }}</span></label>
                                    </div>
                                </div>

                                <div class="col-md-4 col-lg-3">
                                    <div class="form-group">
                                        <label><b>Category : </b> <span>{{ SubsidyDeatilsObject.Category
                                                }}</span></label>
                                    </div>
                                </div>
                            </div>

                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                             <th>Plan Name</th>                                             
                                             <th>Condition Name</th>
                                             <th>Condition</th>
                                             <th>Effective Date</th>  
                                             <th>Expiration Date</th>  
                                                                                  
                                        </tr>
                                    </thead>
                                     <tbody>
                                        <template v-for="item in PlanDetails">

                                            <tr>
                                                <td>{{ item.plan_name }}</td>                                               
                                                <td>{{ item.service_name }}</td>
                                                <td>{{ item.condition }}</td>
                                                <td>{{ item.effective_date }}</td>
                                                <td>{{ item.expiration_date }}</td>
                                                                                           

                                            </tr>

                                        </template>
                                        <tr v-if="PlanDetails.length == 0">
                                            <td :colspan="5" class="text-center text-danger">
                                                {{ $t("NorecordfoundText") }}</td>
                                        </tr>
                                    </tbody> 
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';

export default {
    props: {
        SubsidyDeatilsObject: {
            type: Object,
            require: true
        }

    },

    data() {
        return {
            isLoading: false,
            PlanDetails: [],

        };
    },

    created: async function () {
        debugger
        var vm = this;
        vm.SubsidyDeatilsObject;
        vm.GetSubsidyDetailsByPlan();
    },

    methods: {

        CloseSubsidyDetails() {
            this.$emit('CloseDetails');
        },
        GetSubsidyDetailsByPlan: function () {
            debugger
            var vm = this;
            var Url = `SubsidyId=${vm.SubsidyDeatilsObject.Id}`;
            DataService.GetSubsidyDetailsByPlan(Url).then((response) => {
                if (response.data) {
                    const parsed = JSON.parse(response.data[0]); // parse string to object
                    vm.PlanDetails = parsed.Data; // assign the array inside Data property

                }


            });
        },


    }
};
</script>
