import DataService from '../../../services/DataService'

let leftsearchSchema = [
    {
        fieldName: 'Subsidy Name',
        fieldType: "text",
        fieldIcon: "fa-hand-holding-usd",
        fieldSearchConditionName: ['TBS.Subsidy_Name'],
        isSearch: false,
        value: "",
        listOptions: []
    },
     {
        fieldName: 'Customer Type',
        fieldType: "ddl-check",
        fieldIcon: "fa fa-user-tie",
        fieldSearchConditionName: ['TBS.Category'],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'Subsidy Type',
        fieldType: "ddl-check",
        fieldIcon: "fa-money-bill-wave",
        fieldSearchConditionName: ['TBS.Subsidy_Type'],
        isSearch: false,
        value: "",
        listOptions: []
    }

]


function bindleftCommonSearchdropdown(instance, ModuleName, SubModule, FilterName) {
    
    var vm = instance;
   

    var params = `Type=${FilterName}&Module=${ModuleName}&SubModule=${SubModule}`

    vm.isLoading = true;

    DataService.SubsidyListingFieldData(params).then(response => {

        if (response.data !== null) {

            var json = response.data;

            if (FilterName == 'CUSTOMER_TYPE') {
                vm.leftsearchSchema[1].listOptions = json.map((item) => {
                    const container = {};
                    container.name = item.customer_type_name;
                    container.value = item.customer_type_id.toString();
                    return container;
                })

            }

            else if (FilterName == 'Subsidy_Type') {
                vm.leftsearchSchema[2].listOptions = json.map((item) => {
                    const container = {};
                    container.name = item.name;
                    container.value = item.id.toString();
                    return container;
                })

            }


        }
    })

}
export default { leftsearchSchema , bindleftCommonSearchdropdown};